// import api from '../../api';

export default {

  namespaced: true,

  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// State

  state () {
    return [
      {
        id: 1,
        logo: require('@/assets/img/logo_01.png'),
        img: require('@/assets/img/img_01.jpg'),
        title: 'Livsmedel - Matbutik',
        cats: 'Snacks, Godis, Glass, Livsmedel',
        isSleeping: false,
      },
      {
        id: 2,
        logo: require('@/assets/img/logo_02.png'),
        img: require('@/assets/img/img_02.jpg'),
        title: 'Pizzahuset Spetsamossen',
        cats: 'Pizza, Indisk, Kebab, Falafel',
        isSleeping: false,
      },
      {
        id: 3,
        logo: require('@/assets/img/logo_03.png'),
        img: require('@/assets/img/img_03.jpg'),
        title: 'Senso Rest. & pizzeria',
        cats: 'Pizza, Kebab, Tacos',
        isSleeping: false,
      },
      {
        id: 4,
        logo: require('@/assets/img/logo_04.png'),
        img: require('@/assets/img/img_04.jpg'),
        title: 'Hong Kong',
        cats: 'Kinesisk',
        isSleeping: true,
      },
    ]
  },




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Getters

  getters: {
    getAwakeRestaurants(state) {
      return state.filter(item => !item.isSleeping)
    },
    getSleepRestaurants(state) {
      return state.filter(item => item.isSleeping)
    },
    getRestaurantById: (state) => (id) => {
      return state.find(item => item.id == id)
    },
  },

}
