/// Script -->

<script>

import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'

import Popup from '@/components/Popup.vue'
import Header from '@/components/Header.vue'
import Sidebar from '@/components/Sidebar.vue'
import Card from '@/components/Card.vue'

export default {

  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Basic

  name: 'Restaurants',

  components: {
    Popup,
    Header,
    Sidebar,
    Card,
  },




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Data/Props/Emits

  data() {
    return {
      isPopupClose: true,
      popupType: 'contact',

      isSidebarClose: true,

      foodForThought: true,

      isAnyRestClose: true,
    }
  },




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Computed

  computed: {
    ...mapGetters('restaurants', [
      'getAwakeRestaurants',
      'getSleepRestaurants',
    ]),
  },




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Methods

  methods: {
    openPopup(type) {
      this.popupType = type;
      this.isPopupClose = false;
    },
  },

}

</script>








