.m-restaurants-sort {
  margin-bottom: calc(#{$m-spacer} * 2);
  background-color: $m-color-neutral-bg;
  font-size: $m-font-size-sm;
}

.m-restaurants-sort__container {
  padding: 0;

  @media (min-width: $m-screen-sm-min) {
    padding: 0 15px;
  }
}

.m-restaurants-sort__title {
  padding-left: 15px;
  padding-right: 15px;
  background-color: $m-color-red;
  color: #fff;
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Food For Thought

.m-restaurants-idea {
  margin-bottom: $m-spacer;
  background-color: $m-color-yellow;
  background-repeat: no-repeat;
  background-position: 95% calc(100% + 20px);
  background-size: 124px;
  background-image: $m-restaurants-idea-bg-img;

  color: $m-color-black;
}

.m-restaurants-idea__closer {
  font-size: 24px;

  &,
  &:link,
  &:visited,
  &:hover,
  &:active,
  &:focus {
    color: $m-color-black;
  }
}


.m-quote {
  p {
    font-style: italic;

    &::before {
      content: '\201C';
      margin: 4px;
    }
    &::after {
      content: '\201D';
      margin: 4px;
    }
  }

  footer {
    font-weight: bold;

    &::before {
      content: '\2014';
      margin: 4px;
    }
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Closed Restaurants

.m-restaurants-hr {
  --f8-divider-hr-color: #{$m-bg-2-color};
  --f8-divider-hr-size: 30;
}

.m-restaurants-closes {
  background-color: $m-bg-2-color;
}

.m-restaurants-closes__heading {
  text-align: center;

  svg {
    margin: 0 4px;
    width: 32px;
  }
  svg path,
  svg rect,
  svg circle,
  svg polygon {
    fill: $m-restaurants-strong-color;
  }

  strong {
    color: $m-restaurants-strong-color;
  }
}

</style>
