<template>

<section class="m-popup f8-bar f8-bar--visual-fullscreen" :class="{ 'f8-bar--effect-hide-to-fade': isPopupClose }">
 <div class="m-popup__perspective f8-bar__perspective" @click.self="this.$emit('popupClose')">

   <!-- Hotmat Contact Popup
   ============================================= -->
   <section v-if="popupType == 'contact'" class="m-popup__card f8-card u8-curved u8-box-shadow">
     <header class="m-popup__card-head f8-card__head">
       <i class="bi bi-at u8-text-size-lg u8-line-height-0"></i>
       <h3 class="m-popup__card-heading">Kontakta oss</h3>
     </header>
     <div class="m-popup__card-body f8-card__body u8-chrome-pretty-scroll">
       <p>Har du några frågor eller funderingar så är du välkommen att mejla oss. Vi svarar oftast inom 24 timmar.</p>
       <address>
         Företag: <a class="f8-link f8-link--effect-underline" href="https://www.hotmat.se/">Hotmat.se</a><br>
         E-post : <a class="f8-link f8-link--effect-underline" href="mailto:info@hotmat.se">info@hotmat.se</a>
       </address>
     </div>
   </section>




   <!-- Hotmat Conditions Popup
   ============================================= -->
   <section v-else-if="popupType == 'conditions'" class="m-popup__card f8-card u8-curved u8-box-shadow">
     <header class="m-popup__card-head f8-card__head">
       <i class="bi bi-file-text u8-text-size-lg u8-line-height-0"></i>
       <h3 class="m-popup__card-heading">Köpvillkor</h3>
     </header>
     <div class="m-popup__card-body f8-card__body u8-chrome-pretty-scroll">
       <h4 class="u8-text-color-primary u8-font-weight-bold u8-my">Beställningsvillkor</h4>
       <p>
         Det är restaurangen som bestämmer hur snabbt dom kan laga maten och leveranstiden räknas automatiskt med GPS. Leveranstiden varierar ofta med tiden på dygnet och vi reserverar oss för eventuella dröjsmål till följd av lång orderkö eller trafikproblem.
         <br>
         För särskilt långa leveranstider har restauranger/budfirman ansvaret att meddela kunden så snart som möjligt.
         <br>
         Personliga uppgifter som fylls i beställningsformuläret ska vara korrekt och tillhöra den som beställer maten.  Legitimation måste visas vid leveransen.
         <br>
         Hotmat.se ansvarar inte för utebliven leverans till följd av otillräcklig eller felaktig information i beställningsformuläret.
         <br>
         Man måste ha fyllt 18 år för att beställa från Hotmat.se
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Personuppgifter</h4>
       <p>
         Vid beställning via hotmat.se lagras all nödvändig information (personuppgifter) efter personuppgiftslagen (PuL). Informationen behövs för att ge en trygg och bra tjänst. När en beställning görs via hotmat.se godkänner du samtidigt att all information får lagras, sparas och bearbetas i hotmat.se dataregister enligt PuL. All dataregister får enligt PuL presenteras för parter (såsom restaurangen och utkörningsfirman) vid åtaganden som berör dig som kund/beställare. Uppgifterna kan även komma att användas av hotmat.se i marknadsföringssyfte och för att sammanställa statistik, dvs i syfte att förbättra vår tjänst. Hos hotmat.se lagras inte personuppgifter längre tid än nödvändigt.
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Priser</h4>
       <p>
         Alla priser på Hotmat.se är inklusive moms. Det är restaurangens ansvar att hålla hemsidan uppdaterat med eventuella prisändringar.
         <br>
         Utkörningspriser beror på avståndet mellan restaurangen och leveransadressen och räknas automatiskt med GPS.
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Leverans</h4>
       <p>
         Utkörningen sköts av varje enskild restaurang eller budfirma. Restaurangen/Budfirman har ansvar för utkörningen.
         <br>
         Det är kundens ansvar att vara på plats när maten levereras.
         <br>
         Om maten inte är levererad inom 1 timme efter restaurangens leveranstid ber vi dig att kontakta oss via mail och bifoga beställningens ID så får du pengarna tillbaka.
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Ångerrätt</h4>
       <p>
         Vid beställning av livsmedel gäller ingen ångerrätt. Utdrag ur Konsumentverkets informationsbroschyr om Distans- och hemförsäljningslagen:
         <br>
         Vid distansavtal och hemförsäljningsavtal har du inte någon ångerrätt om du köper livsmedel eller andra varor för ett hushålls löpande förbrukning
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Missbruk</h4>
       <p>
         Kundens IP-adress och andra värdefulla information om Internetleverantör lagras i databasen för att vid eventuellt missbruk av tjänsten kunna spåra upp beställaren. Vi polisanmäler och utreder alla typer av missbruk av våra tjänster på Hotmat.se.
         <br>
         Vi följer Allmänna reklamationsnämndens rekommendationer.
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Betalning och säkerhet</h4>
       <p>
         - Kortbetalning : Betalningen sker via DIBS. Vi lagrar inga uppgifter om din bank eller ditt kort. Vi sparar transaktionens ID som skickas från DIBS till oss för att kunna spåra betalningen vid eventuella problem.
       </p>
       <p>
         - Fakturabetalning : När du betalar via faktura handlar du tryggt och enkelt. Du behöver aldrig uppge dina kortuppgifter, och betalar alltid efter att du fått hem dina varor. Du behöver aldrig lämna ut kortuppgifter
         <br>
         &nbsp;&nbsp; En aviavgift om 0 kr per köp tillkommer. Om betalningen uteblir tillkommer en påminnelseavgift om 60,00 kr samt dröjsmålsränta om 20.00 %. Vid köptillfället görs en kreditprövning som i vissa fall innebär att en kreditupplysning tas. Då kommer du få en kopia på kreditupplysningen per post. Personuppgifter hanteras i enlighet med gällande lagstiftning.
       </p>
       <p>
         - Kontant : Med kontantbetalning beställer du maten och betalar när du får maten.
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Vad är SSL?</h4>
       <p>
         Secure Sockets Layer (SSL) är en säkerhetsmekanism som uppfanns till Netscape och används för att kryptera kommunikationen mellan två enheter. Ofta är ena sidan en webbserver (ex. onlinebutik) och den andra är en webbläsare (ex. Internet Explorer, Chrome eller Firefox). Tanken med att kryptera data som sänds till och från dessa två enheter är att ingen annan på nätverket (oftast internet) ska kunna avlyssna eller manipulera informationen. Handlar man på internet så faller det sig naturligt att man använder SSL för att kryptera ex. kreditkortsinformation.
       </p>

       <h4 class="u8-text-color-primary u8-font-weight-bold">Ändringar</h4>
       <p>
         Hotmat.se har rätt att ändra sina köpvillkor utan att meddela kunden. Kunden godkänner köpvillkoren genom att beställa på hemsidan.
       </p>

       <address class="u8-mb">
         Hotmat.se Sverige AB
			   Huvudkontor: Sågstuvägen 3D, 14149 Huddinge
         <br>
         Org nr : 559001-2893
         <br>
         Momsreg.nr : SE559001289301
         <br>
         E-post : info@hotmat.se
       </address>

       <p>
         För mer information kan du kontakta oss via E-post eller kontakta varje enskild restaurang via telefon.
       </p>
     </div>
   </section>




