////////////////////////////////////////////////////////////////////////////////
/// Script -->

<script>

export default {

  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Basic

  name: 'Popup',




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Data/Props/Emits

  data() {
    return {
      $foodBtn: undefined,
    }
  },

  props: {
    isPopupClose: {
      type: Boolean,
      default: true,
    },
    popupType: {
      type: String,
      default: 'contact',
    },
    payload: {
      type: Object,
      required: false,
      default: () => ({
        // When `popupType` is 'info' or 'terms':
        logo: require('@/assets/img/logo_01.png'),
        title: 'Restaurant Title',
        cats: 'Cat1, Cat2, Cat3',
      }),
    },
  },

  emits: [
    'popupClose',
  ],




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Lifecycle Hooks

  beforeUnmount() {
    $('[data-popup-f8-icon]').f8icon('destroy');

    if (this.popupType == 'food') {
      $('.m-popup__food-btn').f8toggle('destroy');

      if (this.$foodBtn) {
        this.$foodBtn.off('opened.f8toggle');
        this.$foodBtn = undefined;
      }
    }
  },

  // NOTE: Because popups are NOT shown at the beginning by default, so we
  // don't need to initialize the jQuery plugins when mounted() hook is
  // called! Instead we should destroy and then initialize them on each update.
  updated() {
    // First destroy
    $('[data-popup-f8-icon]').f8icon('destroy', true);

    if (this.popupType == 'food') {
      $('.m-popup__food-btn').f8toggle('destroy', true);

      if (this.$foodBtn) {
        this.$foodBtn.off('opened.f8toggle');
        this.$foodBtn = undefined;
      }
    }


    // Now initialize
    $('[data-popup-f8-icon]').f8icon();

    if (this.popupType == 'food') {
      // Why timeout? Because when user is going to close the popup, popup gets
      // updated and this hook is called! So with above codes with destroy the
      // food accordians... But we don't like them to be initialized immediatly
      // after they get destroyed, so that they're state won't be reset and they
      // all get closed if user has already opened one of them... If it happens,
      // it makes our popup look ugly when it's running its disappearing css
      // opacity transition! So we do this, in order let popoup first disapear
      // completely and then this code runs to initalize the accordians again...
      setTimeout(() => {
        $('.m-popup__food-btn').f8toggle();

        // Keep only one food accordian open at a time
        this.$foodBtn = $('.m-popup__food-btn');

        this.$foodBtn.on('opened.f8toggle', e => {
          this.$foodBtn.not(e.target).f8toggle('close');
        });
      }, 300);
    }
  },

}

</script>








