import { createRouter, createWebHashHistory } from 'vue-router'

import Restaurants from '../views/Restaurants.vue'
const Restaurant = () => import('../views/Restaurant.vue') // Let's use dynamic import to generate a separate chunk (startup.[hash].js) for this route (lazy-loading)
const Startup = () => import('../views/Startup.vue')
const Cart = () => import('../views/Cart.vue')
const Checkout = () => import('../views/Checkout.vue')

const routes = [
  {
    path: '/',
    name: 'Restaurants',
    component: Restaurants
  },
  {
    path: '/restaurants',
    redirect: '/'
  },
  {
    path: '/home',
    redirect: '/'
  },
  {
    path: '/restaurants/:restaurant',
    name: 'Restaurant',
    component: Restaurant
  },
  {
    path: '/startup',
    name: 'Startup',
    component: Startup
  },
  {
    path: '/cart',
    name: 'Cart',
    component: Cart
  },
  {
    path: '/checkout',
    name: 'Checkout',
    component: Checkout
  },
]

const router = createRouter({
  // history: createWebHistory(process.env.BASE_URL),
  history: createWebHashHistory(),
  routes,
  scrollBehavior(to, from, savedPosition) {
    // If user herself is navigating with back/forward buttons, just stay where she was before
    if (savedPosition) return savedPosition;

    // Otherwise, scroll to top with a timeout. Why? to give Vuejs time to do
    // our CSS transition on routing first and then change the scroll position.
    return new Promise((resolve, reject) => {
      setTimeout(() => { resolve({ left: 0, top: 0 }) }, 300)
    });
  },
})

export default router
