<script>

export default {

  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Basic

  name: 'Sidebar',




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Data/Props/Emits

  data() {
    return {
      $sidebar: undefined,
      $fader: undefined,
    }
  },

  props: {
    isSidebarClose: Boolean,
  },

  emits: [
    'sidebarClose',
    'sidebarStatusChanged',
    'popupOpen',
  ],




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Lifecycle Hooks

  mounted() {
    $('.m-sidebar').f8bar();
    $('[data-sidebar-f8-switch-daylight]').f8switchdaylight();


    // Keep in sync sidebar and its fader when sidebar is being dragged on mobile devices
    this.$sidebar = $('.m-sidebar');
    this.$fader = $('.m-sidebar__fader');

    this.$sidebar.on('dragged.f8bar', (e, param) => {
      if (param.status === 'open') this.$emit('sidebarStatusChanged', 'open');
      else this.$emit('sidebarStatusChanged', 'close');
      this.$fader.attr('style', (j, style) => { return style.replace(/(--f8-bar-fader-opacity)\s?:\s?[^;]+;?/g, ''); }); // Remove the inline style that we've set while dragging and autodragging were running, now that the dragging is complete
    });

    this.$sidebar.on('dragging.f8bar autodragging.f8bar', (e, param) => {
      this.$fader.css({'--f8-bar-fader-opacity': (param.perc/100)}); // Change the CSS custom property while the block is still dragging or auto dragging
    });
  },

  beforeUnmount() {
    $('.m-sidebar').f8bar('destroy');
    $('[data-sidebar-f8-switch-daylight]').f8switchdaylight('destroy', true);


    this.$sidebar.off('dragged.f8bar').off('dragging.f8bar autodragging.f8bar');
    this.$sidebar = undefined;
    this.$fader = undefined;
  },

}

</script>








