import pluginify      from '../pluginify';
import * as utils     from '../utils/utils';

/**
 * f8-js-toggle - Framework8 toggle block
 * @copyright 2021 MyThemeIsReady. All Rights Reserved.
 *
 * Add toggle functionality to any element on the page. The element can show and
 * hide its target easily.
 *
 *
 * Requirements:
 * - jQuery version 1.9.1 or higher
 * - Pluginify:               ../pluginify.js
 * - Utils:                   ../utils/utils.js
 *
 *
 *
 *
 * @example
 * // HTML simple usage
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * //
 * // <button class="f8-js-toggle" data-f8-target="#target">Toggle</button>
 * // <div id="target">I am the target to be toggled!</div>
 * //
 * // <!-- data-f8-target: String = undefined; (Required)
 * // CSS selector that refers to the target. This is the element that we
 * // add/remove CSS classes for it when needed. -->
 * //
 * // <!-- NOTE: The toggle block manipulates the target's `display`,
 * // `overflow-y`, `transition`, and `height` CSS inline properties. And for a
 * // smooth toggling animation, don't set any padding for the target... You can
 * // have a descendanding child for your target and set the padding of that
 * // element if you need to. -->
 * //
 * // <!-- NOTE: '.f8-js-toggle__target' CSS class will be added to the Target
 * // element automatically. -->
 * //
 * // <!-- NOTE: '.f8-js-toggle__target--state-open' CSS class will be added to
 * // the Target element automatically when it is open; and
 * // '.f8-js-toggle__target--state-close' when it is close. -->
 * //
 * // <!-- NOTE: '.f8-js-toggle--state-active' CSS class will be added to
 * // the Block itself automatically when Target is open; and
 * // '.f8-js-toggle--state-inactive' when Target is close. -->
 *
 *
 *
 *
 * // HTML - Set initial state of the toggle to be open
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * //
 * // <button class="f8-js-toggle" data-f8-open-by-default data-f8-target="#target">Toggle</button>
 * // <div id="target">I am the target to be toggled!</div>
 * //
 * // <!-- data-f8-open-by-default: Boolean = false; (Optional)
 * // A Toggle is closed by default.
 * // So to have it open initially, simply define this option. Then all close
 * // related CSS classes on the Block itself, Target, and Overlay (We will
 * // explain about the Overlay element later as we go on) will be replaced with
 * // the open related classes. Block itself receives
 * // '.f8-js-toggle--state-active', Target receives
 * // '.f8-js-toggle__target--state-open', and Overlay receives
 * // '.f8-js-toggle__overlay--state-blur' if it's available. -->
 *
 *
 *
 *
 * // HTML custom toggling behavior
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * //
 * // <style media="screen">
 * //   #target {
 * //     opacity: 0;
 * //     transition: opacity .3s;
 * //     pointer-events: none;
 * //   }
 * //   #target.target--state-open {
 * //     opacity: 1;
 * //   }
 * // </style>
 * //
 * // <button class="f8-js-toggle" data-f8-toggle-by-my-own data-f8-target="#target" data-f8-target-open-class="target--state-open">Toggle</button>
 * // <div id="target">I am the target to be toggled!</div>
 * //
 * // <!-- NOTE: Unlike automatic toggling behavior, the custom toggling won't
 * // manipulate any styles of the target... So feel free to style it in any way
 * // that you like. Cheers! -->
 * //
 * // <!-- data-f8-toggle-by-my-own: Boolean = false; (Optional)
 * // If it's true, it toggles based on your own CSS classes and the automatic
 * // toggling animation won't happen! This is due to giving you more freedom in
 * // creating different toggling effects based on CSS animations. -->
 * //
 * // <!-- data-f8-target-open-class: String = 'f8-js-toggle__target--state-open'; (Optional)
 * // CSS class that makes your target be opened. This CSS class will be added
 * // to the target element when it's open, and removed when it's close. -->
 * //
 * // <!-- data-f8-target-close-class: String = 'f8-js-toggle__target--state-close'; (Optional)
 * // CSS class that makes your target be closed. This CSS class will be added
 * // to the target element when it's close, and removed when it's open. -->
 *
 *
 *
 *
 * // HTML custom toggling behavior - More options
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * //
 * // <style media="screen">
 * //   .f8-js-toggle,
 * //   #target,
 * //   body:after {
 * //     transition: all .3s;
 * //   }
 * //
 * //   .f8-js-toggle {
 * //     position: relative;
 * //     z-index: 9998;
 * //   }
 * //   .f8-js-toggle.state--active {
 * //     color: #fff;
 * //     background-color: #333;
 * //   }
 * //
 * //   #target {
 * //     position: relative;
 * //     z-index: 9999;
 * //     opacity: 0;
 * //     pointer-events: none;
 * //   }
 * //   #target.target--state-open {
 * //     opacity: 1;
 * //   }
 * //
 * //   body:after {
 * //     position: fixed;
 * //     top: 0;
 * //     left: 0;
 * //     width: 100%;
 * //     height: 100%;
 * //     content: '';
 * //     background-color: rgba(0, 0, 0, .2);
 * //     opacity: 0;
 * //     visibility: hidden;
 * //   }
 * //   body.body--state-blur:after {
 * //     opacity: 1;
 * //     visibility: visible;
 * //   }
 * // </style>
 * //
 * // <button class="f8-js-toggle" data-f8-toggle-by-my-own data-f8-active-class="state--active"
 * // data-f8-target="#target" data-f8-target-open-class="target--state-open"
 * // data-f8-overlay="body" data-f8-overlay-blur-class="body--state-blur">
 * // Toggle
 * // </button>
 * // <div id="target">I am the target to be toggled!</div>
 * //
 * // <!-- data-f8-active-class: String = 'f8-js-toggle--state-active'; (Optional)
 * // CSS class that changes your block to an active state. It will be added
 * // to the Block when Target is open, and removed when Target is close. -->
 * //
 * // <!-- data-f8-inactive-class: String = 'f8-js-toggle--state-inactive'; (Optional)
 * // CSS class that changes your block to an inactive state. It will be
 * // added to the Block when Target is close, and removed when Target is open. -->
 * //
 * // <!-- data-f8-overlay: String = undefined; (Optional)
 * // CSS selector which is known as the Overlay element of the block. By
 * // defining this option you can toggle your own custom CSS classes on another
 * // element known as the 'overlay' element. e.g. You may like to make your
 * // page's background dark when your website's menu is open... So that's the
 * // time this option comes in handy. -->
 * //
 * // <!-- data-f8-overlay-blur-class: String = 'f8-js-toggle__overlay--state-blur'; (Optional)
 * // CSS class that changes your Overlay to a blur state. It will be added
 * // to the Overlay when Target is open, and removed when Target is close. -->
 * //
 * // <!-- data-f8-overlay-focus-class: String = 'f8-js-toggle__overlay--state-focus'; (Optional)
 * // CSS class that changes your Overlay to a focus state. It will be added
 * // to the Overlay when Target is close, and removed when Target is open. -->
 * //
 * // <!-- NOTE: '.f8-js-toggle__overlay--state-blur' CSS class will be added to
 * // the Overlay automatically when Target is open; and
 * // '.f8-js-toggle__overlay--state-focus' when Target is close. -->
 *
 *
 *
 *
 * // HTML - Using the default state CSS classes
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * //
 * // <style media="screen">
 * //   .f8-js-toggle,
 * //   .f8-js-toggle__overlay:after {
 * //     transition: all .3s;
 * //   }
 * //
 * //   .f8-js-toggle {
 * //     position: relative;
 * //     z-index: 9998;
 * //   }
 * //   .f8-js-toggle--state-active {
 * //     color: #fff;
 * //     background-color: #333;
 * //   }
 * //
 * //   .f8-js-toggle__target {
 * //     position: relative;
 * //     z-index: 9999;
 * //   }
 * //   .f8-js-toggle__target--state-open {
 * //     background-color: yellow;
 * //     transition: background-color .3s;
 * //   }
 * //
 * //   .f8-js-toggle__overlay:after {
 * //     position: fixed;
 * //     top: 0;
 * //     left: 0;
 * //     width: 100%;
 * //     height: 100%;
 * //     content: '';
 * //     background-color: rgba(0, 0, 0, .2);
 * //     opacity: 0;
 * //     visibility: hidden;
 * //   }
 * //   .f8-js-toggle__overlay--state-blur:after {
 * //     opacity: 1;
 * //     visibility: visible;
 * //   }
 * // </style>
 * //
 * // <button class="f8-js-toggle" data-f8-toggle-by-my-own data-f8-target="#target" data-f8-overlay="body">Toggle</button>
 * // <div id="target">I am the target to be toggled!</div>
 *
 *
 *
 *
 * // HTML - Other options
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * //
 * // <button class="f8-js-toggle" data-f8-target="#target1"
 * // data-f8-toggle-on-mouse-over data-f8-closer="body"
 * // data-f8-toggle-only-on-element-itself>
 * // Toggle
 * // </button>
 * // <div id="target1">I am the target to be toggled!</div>
 * //
 * // <!-- data-f8-closer: String = undefined; (Optional) CSS selector that refers to another element that can close the toggle on its click/mouseenter, rather than the toggle element itself -->
 * // <!-- data-f8-toggle-on-mouse-over: Boolean = false; (Optional) Toggle by mouse over/out on the toggle block instead of clicking it -->
 * //
 * //
 * // <button class="f8-js-toggle" data-f8-target="#target2" data-f8-toggle-only-on-element-itself>
 * // Click me myself to toggle <span style="color: red;"> But don't click me as a child element!</span>
 * // </button>
 * // <div id="target2">I am the target to be toggled!</div>
 * //
 * // <!-- data-f8-toggle-only-on-element-itself: Boolean = false; (Optional)
 * // Toggle ONLY IF user clicks the element itself! I mean the toggling
 * // behaviour won't happen if user clicks a child element inside of the
 * // element itself.
 * // NOTE: This option doesn't make any difference when
 * // 'data-f8-toggle-on-mouse-over' option is true, and it also doesn't call
 * // the preventDefault() function on click. e.g., This option is useful for
 * // the times that you're going to toggle a modal! The toggle block is inside
 * // of a modal (target of the block). Now the block fades out the whole page
 * // and at the center of itself, it puts the modal's content... In this case
 * // we use this option, to open/close the modal ONLY IF user clicks the faded
 * // part of the block... not when she's interacting with the modal's content
 * // which are the children of the block itself... -->
 *
 *
 *
 *
 * // HTML - Manipulating the toggle behavior by CSS media queries
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * //
 * // <style media="screen">
 * //   @media (min-width: 768px) {
 * //     .f8-js-toggle {
 * //       display: none;
 * //     }
 * //     .f8-js-toggle__target {
 * //       display: block !important;
 * //       height: auto !important;
 * //     }
 * //   }
 * // </style>
 * //
 * // <button class="f8-js-toggle" data-f8-target="#target">Toggle</button>
 * // <div id="target">I am the target to be toggled!</div>
 * //
 * // <!-- NOTE: If you're using the default toggling animation (I mean, you
 * // didn't define 'data-f8-toggle-by-css' option and also like the Target element
 * // to be visible in certain break-points in your CSS, you should override
 * // 'height' and 'display' CSS properties by using the !important flag to make
 * // the Target element visible. Because the default animation writes forcing
 * // inline CSS styles... So as you're trying to override them, you need to
 * // have the !important flag for your own CSS rules (Only 'display' and
 * // 'height' properties need to have the !important flag). -->
 *
 *
 *
 *
 * // HTML - Manipulating the toggle behavior by CSS media queries - The complex one
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * //
 * // <style media="screen">
 * //   .f8-js-toggle,
 * //   .f8-js-toggle__overlay:after {
 * //     transition: all .3s;
 * //   }
 * //
 * //   .f8-js-toggle {
 * //     position: relative;
 * //     z-index: 9998;
 * //   }
 * //   .f8-js-toggle--state-active {
 * //     color: #fff;
 * //     background-color: #333;
 * //   }
 * //
 * //   .f8-js-toggle__target {
 * //     position: relative;
 * //     z-index: 9999;
 * //   }
 * //   .f8-js-toggle__target--state-open {
 * //     background-color: yellow;
 * //     transition: background-color .3s;
 * //   }
 * //
 * //   .f8-js-toggle__overlay:after {
 * //     position: fixed;
 * //     top: 0;
 * //     left: 0;
 * //     width: 100%;
 * //     height: 100%;
 * //     content: '';
 * //     background-color: rgba(0, 0, 0, .2);
 * //     opacity: 0;
 * //     visibility: hidden;
 * //   }
 * //   .f8-js-toggle__overlay--state-blur:after {
 * //     opacity: 1;
 * //     visibility: visible;
 * //   }
 * //
 * //   @media (min-width: 768px) {
 * //     .f8-js-toggle {
 * //       display: none;
 * //     }
 * //     .f8-js-toggle__target {
 * //       display: block !important;
 * //       height: auto !important;
 * //     }
 * //   }
 * // </style>
 * //
 * // <button class="f8-js-toggle" data-f8-target="#target" data-f8-overlay="body">Toggle</button>
 * // <div id="target">I am the target to be toggled!</div>
 * //
 * //
 * // <!-- NOTE: Put the scripts after loading jquery and the plugin itself -->
 * //
 * // <script>
 * // jQuery(document).ready(function($) {
 * //
 * //   var mq = window.matchMedia('(min-width: 768px)');
 * //   var mqClosedToggle = false;
 * //   var isToggleOpen = ($('.f8-js-toggle__target').hasClass('f8-js-toggle__target--state-open')) ? true : false;
 * //   $('.f8-js-toggle').on('opened.f8toggle', e => { isToggleOpen = true; });
 * //   $('.f8-js-toggle').on('closed.f8toggle', e => { if (!mqClosedToggle) isToggleOpen = false; });
 * //
 * //   mq.addListener(function() {
 * //     if (mq.matches) {
 * //       $('.f8-js-toggle').f8toggle('close');
 * //       mqClosedToggle = true;
 * //     } else {
 * //       if (isToggleOpen) $('.f8-js-toggle').f8toggle('open');
 * //       mqClosedToggle = false;
 * //     }
 * //   });
 * //
 * // });
 * // </script>
 * //
 * // <!-- NOTE: As you can see in this example, if you didn't define
 * // 'data-f8-toggle-by-my-own' option, though have set some CSS rules for Target,
 * // Overlay, or the Toggle Block itself when they are in different states, and
 * // also manipulated the Target behavior by CSS media queries... Then you
 * // should also consider closing the Toggle Block by JavaScript when you hit
 * // your media query. e.g., If you have darken the Overlay by
 * // '.f8-js-toggle__overlay--state-blur' CSS class but like it to be normal
 * // again when you're in large screen, then you should close the Toggle Block
 * // in large screen, so that the state CSS class
 * // ('.f8-js-toggle__overlay--state-blur') can be removed from your Overlay
 * // element. -->
 * //
 * // <!-- NOTE: If you didn't use the default animation (I mean you have
 * // defined 'data-f8-toggle-by-my-own' option), then you may not need to close the
 * // Toggle by JavaScript! Because in this case, there's no animation related to
 * // JavaScript and everything is going to be done all in CSS by your own CSS
 * // rules... So you can simply do custom CSS media queries and also open/close
 * // the Toggle by your own CSS state classes in any way that you wish. -->
 *
 *
 *
 *
 * // HTML - Making a simple accordion
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * //
 * // <button class="f8-js-toggle u8-d-block" data-f8-target="#target01">Toggle #01</button>
 * // <div id="target01">I am the target #01 to be toggled!</div>
 * // <button class="f8-js-toggle u8-d-block" data-f8-target="#target02">Toggle #02</button>
 * // <div id="target02">I am the target #02 to be toggled!</div>
 * // <button class="f8-js-toggle u8-d-block" data-f8-target="#target03">Toggle #03</button>
 * // <div id="target03">I am the target #03 to be toggled!</div>
 * //
 * //
 * // <!-- NOTE: Put the scripts after loading jquery and the plugin itself -->
 * //
 * // <script>
 * // jQuery(document).ready(function($) {
 * //
 * //   // Close another one when the other one is open.
 * //   $('.f8-js-toggle').on('opened.f8toggle', e => {
 * //     $('.f8-js-toggle').not(e.target).f8toggle('close');
 * //   });
 * //
 * // });
 * // </script>
 *
 *
 *
 * // JS usage
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 * // //////////////////////////////////////////////////////////////////////////
 *
 * // If you've set 'initializeF8BlocksManually' variable in your HTML page to
 * // `true`, then you need to initialize the block manually yourself to enable
 * // its JavaScript functionalities.
 * $('.f8-js-toggle').f8toggle();
 *
 * // Of course when you initialize the block, you can pass it your options object too.
 * // $('.f8-js-toggle').f8toggle({f8OpenByDefault: true});
 *
 * // Destroy the initialized block
 * // NOTE: If the method's 'keepCssStyles' argument is set to 'true' (it's
 * // 'false' by default), then any added CSS classes to the elements won't be
 * // removed on destroy.
 * // $('.f8-js-toggle').f8toggle('destroy', false);
 *
 *
 * // The following methods can be called
 * $('.f8-js-toggle').f8toggle('toggle');
 * $('.f8-js-toggle').f8toggle('open');
 * $('.f8-js-toggle').f8toggle('close');
 * $('.f8-js-toggle').f8toggle('updateState');
 *
 * // The following events will be triggered on special occasions
 * $('.f8-js-toggle').on('opened.f8toggle', e => { });
 * $('.f8-js-toggle').on('closed.f8toggle', e => { });
 *
 *
 * // NOTE: If there are more than one Toggle blocks with the SAME target, you
 * // may like to update the state of other blocks when one of the blocks
 * // toggles the target... That's the time 'updateState' method actually comes
 * // in.
 * // NOTE: Also note that in such cases, if you've set the 'f8OpenByDefault'
 * // option to true (or you've set it via 'data-f8-open-by-default' data
 * // attribute) for one of the blocks, you NEED to set it for the rest of the
 * // blocks too, so that they can work properly and in sync.
 * // $('.f8-js-toggle').on('opened.f8toggle', e => { setTimeout(() => { $('.f8-js-toggle').not(e.target).f8toggle('updateState'); }, 50); });
 * // $('.f8-js-toggle').on('closed.f8toggle', e => { setTimeout(() => { $('.f8-js-toggle').not(e.target).f8toggle('updateState'); }, 50); });
 */
class Toggle {

  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Properties

  _properties(element, options) {
    this.$element                            = $(element);
    this.options                             = this._getOptions(options);
    this.$target                             = $(options.f8Target);
    this.$overlay                            = $(options.f8Overlay);
    this.$closer                             = $(options.f8Closer);
  }




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Constructor

  constructor(element, options) {
    this._properties(element, options);

    // Check if target is ok, then continue. Because a toggle block without a
    // target is actually meaningless! And if it's ok, add its main modifier
    // (f8-js-toggle__target).
    if (!this.$target.length) throw new Error('.f8-js-toggle blocks require a valid "f8Target" option');
    else this.$target.addClass('f8-js-toggle__target');

    // If $overlay is ok, add its main modifier (f8-js-toggle__overlay)
    if (this.$overlay.length) this.$overlay.addClass('f8-js-toggle__overlay');

    // Set the initial state of the toggle
    this._setInitState();

    // Toggle the target when our toggle element (this.$element) is clicked/mouseovered
    this._setElementListener();

    // Let's add the element closing listener to the $closer element. We call
    // this function on constructor so that we can have its listener ready from
    // the beginning if the element is already open on the page.
    this._addCloserListener();
  }




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Static constant

  static get DEFAULTS() {
    return {
      f8ActiveClass:                           'f8-js-toggle--state-active',
      f8InactiveClass:                         'f8-js-toggle--state-inactive',

      f8OpenByDefault:                         false,
      f8ToggleByMyOwn:                         false,
      f8ToggleOnMouseOver:                     false,
      f8ToggleOnlyOnElementItself:             false,

      f8Target:                                undefined,
      f8TargetOpenClass:                       'f8-js-toggle__target--state-open',
      f8TargetCloseClass:                      'f8-js-toggle__target--state-close',

      f8Overlay:                               undefined,
      f8OverlayBlurClass:                      'f8-js-toggle__overlay--state-blur',
      f8OverlayFocusClass:                     'f8-js-toggle__overlay--state-focus',

      f8Closer:                                undefined,
    }
  }




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Private functions

  _getOptions(options) {
    // The following options should be boolean. Now if the user mentions them
    // via data API without specifying their value, then they will be string, so
    // we check that if they are string, let's convert them into boolean.
    options.f8OpenByDefault =                           utils.toBoolean(options.f8OpenByDefault);
    options.f8ToggleByMyOwn =                           utils.toBoolean(options.f8ToggleByMyOwn);
    options.f8ToggleOnMouseOver =                       utils.toBoolean(options.f8ToggleOnMouseOver);
    options.f8ToggleOnlyOnElementItself =               utils.toBoolean(options.f8ToggleOnlyOnElementItself);

    return options;
  }


  _setInitState() {
    // First things first! Set the state classes.
    if (this.options.f8OpenByDefault) this._handleStateClasses('open');
    else this._handleStateClasses('close');

    // Now if user liked to do the toggling herself, then we don't need to do
    // anything else! Just return.
    if (this.options.f8ToggleByMyOwn) return;

    // Now set some inline styles that we need later for the target.
    this.$target.css({
      overflowY: 'hidden',
      transition: 'height .35s ease',
      // padding: '0',
    });

    // Now finally, if target must be closed, then display none it! Otherwise,
    // just do nothing and let it to have its own display.
    if (!this.options.f8OpenByDefault) {
      this.$target.css({ display: 'none', height: '0' });
      // this.$target[0].style.setProperty('display', 'none', 'important'); // We do not set the inline style with the important flag! Because user herself may like to override the styles...
    } else {
      let {height: th} = this._getTargetInfo();
      this.$target.css('height', `${th}px`);
    }
  }

  _getTargetInfo() {
    return {
      width: this.$target.outerWidth(),
      height: this.$target.outerHeight(),
      display: this.$target.css('display'),
    };
  }


  _setElementListener() {
    if (this.options.f8ToggleOnMouseOver) {

      this.$element.off('mouseenter.f8toggle').on('mouseenter.f8toggle', e => {this.toggle();});

    } else {

      this.$element.off('click.f8toggle').on('click.f8toggle', e => {
        if (this.options.f8ToggleOnlyOnElementItself) {
          if (e.target === this.$element[0]) this.toggle();
        } else {
          e.preventDefault();
          this.toggle();
        }
      });

    }
  }


  _handleStateClasses(status) {
    switch (status) {
      case 'open':
        this.$element.addClass(this.options.f8ActiveClass).removeClass(this.options.f8InactiveClass);
        this.$target.addClass(this.options.f8TargetOpenClass).removeClass(this.options.f8TargetCloseClass);
        if (this.$overlay.length) this.$overlay.addClass(this.options.f8OverlayBlurClass).removeClass(this.options.f8OverlayFocusClass);
        break;
      case 'close':
        this.$element.removeClass(this.options.f8ActiveClass).addClass(this.options.f8InactiveClass);
        this.$target.removeClass(this.options.f8TargetOpenClass).addClass(this.options.f8TargetCloseClass);
        if (this.$overlay.length) this.$overlay.removeClass(this.options.f8OverlayBlurClass).addClass(this.options.f8OverlayFocusClass);
        break;
    }
  }

  _doToggling(status) {
    this._dispatchEvent(status);
    this._handleStateClasses(status);

    // Now if status is open, then let's also add the closer listener if it is
    // available... Why to set it with a timeout? Because when user clicks to
    // open the target, we cannot immediately add the closing listener to close
    // it again! Now why to check the status to add or remove the closer
    // listener? Well, because if the closer option is set and we need to close
    // the toggle when closer is clicked... Then if user clicks the toggle
    // itself to close it, then the closer listener won't be called, so that it
    // cannot close the toggle and then also remove itself listener! So in that
    // case, we should remove its listener here...
    if (this.$closer.length) {
      if (status == 'open') setTimeout(() => { this._addCloserListener(); }, 10);
      else this.$closer.off(`${ (this.options.f8ToggleOnMouseOver) ? 'mousemove' : 'click' }.f8toggle`);
    }

    if (this.options.f8ToggleByMyOwn) return;

    this._doTogglingManually(status);
  }

  _doTogglingManually(status) {
    if (status == 'open') {
      // Remove the transition complete listener as soon as we're going to open
      // the target. Why? Because user may play around and try to quickly open
      // and close the toggle... So in that case when the target's transition is
      // not complete yet on close, she opens it and then the listener gets
      // called!
      this.$target.off('transitionend.f8toggle webkitTransitionEnd.f8toggle oTransitionEnd.f8toggle');

      // Remove forced inline styles
      utils.removeInlineStyles(this.$target, 'display', 'height');

      // Now get the original height of the target
      let {height: th} = this._getTargetInfo();

      // Set it's height to 0 again
      this.$target.css('height', '0');

      // And after a little while, set the needed height amount. NOTE: If we set
      // the height amount immediately, the CSS transition won't work!
      setTimeout(() => { this.$target.css('height', `${th}px`); }, 10);
    } else {
      // Set target's height to 0
      this.$target.css('height', '0');

      // And when the height transition is done, set it's display to none too
      this.$target.one('transitionend.f8toggle webkitTransitionEnd.f8toggle oTransitionEnd.f8toggle', e => {
        this.$target.css('display', 'none');
      });
    }
  }

  _dispatchEvent(status) {
    // Handle our event dispatching
    if (status == 'open') this.$element.trigger($.Event('opened.f8toggle'));
    else this.$element.trigger($.Event('closed.f8toggle'));
  }


  _addCloserListener() {
    if (!this.$closer.length || !this.$target.hasClass(this.options.f8TargetOpenClass)) return;

    // If f8ToggleOnMouseOver option is true, we should add 'mousemove' event to
    // the document, otherwise we should add click event.
    let event = (this.options.f8ToggleOnMouseOver) ? 'mousemove' : 'click';
    this.$closer.off(`${event}.f8toggle`).on(`${event}.f8toggle`, e => {
      let targetItself;
      let elementItself;

      // Let's see if the interacted element is our toggle element's
      // target(this.$target) itself. If it's not, then see if it is a
      // descendant child inside of the target by finding the parent of the
      // interacted element and seeing if the parent is our target or not.
      if (e.target == this.$target[0]) targetItself = e.target;
      else targetItself = $( $(e.target).parents(this.options.f8Target) )[0];

      // Do the same thing for element as well! I mean see if the interacted
      // element is our element itself or its descendant child.
      if (e.target == this.$element[0]) elementItself = e.target;
      else elementItself = $( $(e.target).parents(`.f8-js-toggle[data-f8-target="${this.options.f8Target}"]`) )[0];

      // If the interacted element is NOT the this.$target itself or the parent
      // of it on the page is NOT this.$target, or it's NOT the element itself
      // or the parent of it is NOT the element; then it means that user has
      // actually clicked/mouseovered outside of the whole this.$target AND
      // element itself.
      if (!targetItself && !elementItself) {
        this.close();
        $(this).off(e);
      }
    });
  }




  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////////////////
  /// Methods

  open() {
    if (this.$target.hasClass(this.options.f8TargetOpenClass)) return;
    this._doToggling('open');
  }

  close() {
    if (!this.$target.hasClass(this.options.f8TargetOpenClass)) return;
    this._doToggling('close');
  }

  toggle() {
    if (this.$target.hasClass(this.options.f8TargetOpenClass)) this.close();
    else this.open();
  }

  updateState() {
    let status = (this.$target.hasClass(this.options.f8TargetOpenClass)) ? 'open' : 'close';
    this._handleStateClasses(status);

    // We should also add the on closer close listener here as well! Why?
    // Because when 2 toggle buttons are referring to 1 target, when toggle#1
    // opens the target, and toggle#2 has the closer option defined, then
    // updating its CSS classes is not enough! We should also add the closer
    // listener as well so that toggle#2 can close the target on closer
    // click/mouseover again.
    setTimeout(() => { this._addCloserListener(); }, 10);
  }

  destroy(keepCssStyles = false) {
    // Remove all probable added styles from any element
    if (!keepCssStyles) {
      this.$element.removeClass(this.options.f8ActiveClass).removeClass(this.options.f8InactiveClass);
      this.$target.removeClass('f8-js-toggle__target').removeClass(this.options.f8TargetOpenClass).removeClass(this.options.f8TargetCloseClass);
      if (this.$overlay.length) this.$overlay.removeClass('f8-js-toggle__overlay').removeClass(this.options.f8OverlayBlurClass).removeClass(this.options.f8OverlayFocusClass);

      if (!this.options.f8ToggleByMyOwn) utils.removeInlineStyles(this.$target, 'display', 'height', 'overflow-y', 'transition');
    }


    // Remove listeners from any element
    this.$element.off('mouseenter.f8toggle').off('click.f8toggle');
    this.$target.off('transitionend.f8toggle webkitTransitionEnd.f8toggle oTransitionEnd.f8toggle');
    if (this.$closer.length) this.$closer.off('mousemove.f8toggle').off('click.f8toggle');


    // Null all saved variables
    this.$element                            = undefined;
    this.options                             = undefined;
    this.$target                             = undefined;
    this.$overlay                            = undefined;
    this.$closer                             = undefined;
  }

}




// Create our jQuery plugin
pluginify('f8toggle', Toggle);

// If there's no initializeF8BlocksManually variable which is true, then run the
// plugin for all the blocks in the page
if (typeof window.initializeF8BlocksManually !== 'boolean' || window.initializeF8BlocksManually === false) {
  jQuery(document).ready( $ => {
    $('.f8-js-toggle').f8toggle();
  });
}
